import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { SafeResourceUrl } from "@angular/platform-browser";
import { AlertController, ToastController } from "@ionic/angular";
import { environment } from "src/environments/environment";

import { User } from "./../models/user";
import { AuthService } from "./auth.service";
import { RestService } from "./rest.service";
import { UploadService } from "./upload.service";
import { firstValueFrom } from "rxjs";
import { I18nService } from "./i18n.service";
import { Config } from "./config.service";

const httpOptions = {
  headers: new HttpHeaders({ "Content-Type": "application/json" }),
};

@Injectable({ providedIn: "root" })
export class UserService {
  constructor(
    private rest: RestService,
    private uploadSvc: UploadService,
    private auth: AuthService,
    private toast: ToastController,
    private http: HttpClient,
    private i18n: I18nService,
    private alertCtrl: AlertController
  ) {}

  async getUser(id: User["id"] | User["username"]): Promise<User> {
    try {
      return (await this.rest.get(`user/${id}`)) as User;
    } catch (e) {
      throw new Error("No se puede obtener el usuario");
    }
  }

  async getPublicUser(id: User["id"] | User["username"]): Promise<User> {
    try {
      return (await firstValueFrom(
        this.http.get(`${environment.root}api/public-user/${id}`, httpOptions)
      )) as User;
    } catch (e) {
      throw new Error("No se puede obtener el usuario");
    }
  }

  async updateUser(user: User): Promise<User> {
    try {
      user = (await this.rest.put("user", user)) as User;
      this.auth.setAuthUser(user);
      return user;
    } catch (e) {
      throw new Error("No se puede actualizar el usuario");
    }
  }

  async uploadAvatar(file: Blob) {
    const formData: FormData = new FormData();
    formData.set("avatar", file);
    const user = (await this.uploadSvc.upload("avatar", formData)) as User;
    localStorage.setItem("currentUser", JSON.stringify(user));
    return user;
  }

  async setAvatar(src: SafeResourceUrl) {
    try {
      const user = (await this.rest.put("avatar", { avatar: src })) as User;
      this.auth.setAuthUser(user);
      return user;
    } catch (e) {
      throw new Error("No se puede actualizar el usuario");
    }
  }

  async deleteAvatar(src: SafeResourceUrl) {
    try {
      const user = (await this.rest.put("delete-avatar", {
        avatar: src,
      })) as User;
      this.auth.setAuthUser(user);
      return user;
    } catch (e) {
      throw new Error("No se puede actualizar el usuario");
    }
  }

  async setCoordinates(longitude: number, latitude: number) {
    return (await this.rest.put("coordinates", {
      longitude,
      latitude,
    })) as Promise<User["coordinates"]>;
  }

  setManualGeolocation(country: string, city: string) {
    return this.rest.put("manual-geolocation", { country, city }) as Promise<
      User["coordinates"]
    >;
  }

  getRadarUsers(
    page = 1,
    ratio = -1,
    options?: { identity: boolean; range: boolean; connection: boolean },
    location?: { country: string; city: string }
  ) {
    return this.rest.put("radar", {
      page,
      ratio,
      options,
      location,
    }) as Promise<User[]>;
  }

  searchUsers(query: string, order: "distance" | "match", page: number) {
    return this.rest.post(`search?page=${page}`, { query, order }) as Promise<
      User[]
    >;
  }

  searchUsersBySlug(slug: string, order: "distance" | "match", page: number) {
    return this.rest.post(`search-by-slug?page=${page}`, {
      slug,
      order,
    }) as Promise<User[]>;
  }

  searchUsernames(query: string) {
    return this.rest.get(`search-usernames/${query}`) as Promise<User[]>;
  }

  activateUser(verification_code: string) {
    return this.rest.put("activation", { verification_code }) as Promise<User>;
  }

  disableUser(code: string, note: string) {
    return this.rest.put("disable", {
      code,
      note,
    }) as Promise<User>;
  }

  removeAccount(code: string, note: string) {
    return this.rest.put("remove-account", {
      code,
      note,
    }) as Promise<User>;
  }

  changePassword(old_password: string, new_password: string) {
    return this.rest.put("password", {
      old_password,
      new_password,
    }) as Promise<User>;
  }

  changeEmail(old_email: string, new_email: string) {
    return this.rest.put("email", {
      old_email,
      new_email,
    }) as Promise<User>;
  }

  changeUsername(new_username: string) {
    return this.rest.put("username", {
      new_username,
    }) as Promise<User>;
  }

  getLikes() {
    return this.rest.get("likes") as Promise<User[]>;
  }

  like(id: User["id"]) {
    return this.rest.put("like", { user: id }) as Promise<User>;
  }

  unlike(id: User["id"]) {
    return this.rest.delete(`like/${id}`) as Promise<User>;
  }

  getBlocks() {
    return this.rest.get("blocks") as Promise<User[]>;
  }

  unblock(id: User["id"]) {
    return this.rest.delete(`block/${id}`) as Promise<User[]>;
  }

  report(id: User["id"], note?: string) {
    return this.rest.put("report", { user: id, note });
  }

  getHides() {
    return this.rest.get("hides") as Promise<User[]>;
  }

  hide(id: User["id"]) {
    return this.rest.put("hide", { user: id });
  }

  view(id: User["id"]) {
    this.rest.put("view", { user: id });
  }

  unhide(id: User["id"]) {
    return this.rest.delete(`hide/${id}`) as Promise<User[]>;
  }

  async showRole(user: User) {
    let message = "";
    if (user.roles?.includes("ROLE_ADMIN")) {
      message = this.i18n.translate("administrador");
    } else if (user.roles?.includes("ROLE_MASTER")) {
      message = this.i18n.translate("moderador");
    } else if (user.roles?.includes("ROLE_PATREON")) {
      message = "Patreon";
    } else if (user.roles.includes("ROLE_DEMO")) {
      message = "FrikiBot";
    } else if (user.verified) {
      message = this.i18n.translate("cuenta-verificada");
    }

    (
      await this.toast.create({
        message,
        duration: 1500,
        position: "middle",
        color: this.getRoleColor(user),
      })
    ).present();
  }

  getRoleColor(user?: User) {
    if (!user) {
      user = this.auth.currentUserValue;
    }

    if (user.roles.includes("ROLE_ADMIN")) {
      return "danger";
    } else if (user.roles.includes("ROLE_MASTER")) {
      return "tertiary";
    } else if (user.roles.includes("ROLE_PATREON")) {
      return "patreon";
    } else if (user.roles.includes("ROLE_DEMO")) {
      return "medium";
    } else if (user.verified) {
      return "secondary";
    }
    return "light";
  }

  getRoleIcon(user?: User) {
    if (!user) {
      user = this.auth.currentUserValue;
    }

    if (user) {
      const roleIconMap = {
        ROLE_ADMIN: "shield-checkmark",
        ROLE_MASTER: "shield-checkmark",
        ROLE_PATREON: "/assets/icon/patreon_coral.svg",
        ROLE_DEMO: "/assets/icon/smart_toy_white_24dp.svg",
      };

      for (let role in roleIconMap) {
        if (user.roles.includes(role)) {
          return roleIconMap[role];
        }
      }

      if (user.verified) {
        return "checkmark-circle";
      }
    }

    return false;
  }

  async linkToPatreon(code: string) {
    await this.rest.put("link-patreon", { oauth_code: code });
  }

  async unsubscribe(code: string) {
    try {
      return firstValueFrom(
        this.http.get(`${environment.root}api/unsubscribe/${code}`, httpOptions)
      );
    } catch (e) {
      throw new Error("Error al desuscribirse");
    }
  }

  subscribePremium(premium_expiration: string) {
    return this.rest.put("premium", { premium_expiration }) as Promise<User>;
  }

  async setTutorialConfig(tutorial_config: Config["tutorial"]) {
    const user = (await this.rest.put("tutorial-config", {
      tutorial_config,
    })) as User;
    this.auth.setAuthUser(user);
  }

  getOrientations() {
    return [
      "heterosexual",
      "homosexual",
      "bisexual",
      "pansexual",
      "queer",
      "demisexual",
      "sapiosexual",
      "asexual",
    ];
  }

  getPronouns() {
    return ["hehim", "sheher", "theythem"];
  }

  getRelationships() {
    return ["monogamy", "non-monogamy"];
  }

  getStatus() {
    return ["single", "in-a-relationship", "stable-couple", "married"];
  }

  getGenders() {
    return [
      "woman",
      "man",
      "transgender-woman",
      "transgender-man",
      "agender",
      "androgynous",
      "genderfluid",
      "bigender",
      "non-binary",
      "non-conforming",
      "pangender",
      "polygender",
      "intergender",
    ];
  }

  getConnections() {
    return [
      "friends",
      "casual-sex",
      "friends-with-benefits",
      "formal-relationship",
    ];
  }

  getLanguages() {
    // Listado de idiomas principales para la aplicación
    return [
      { value: "es", name: "spanish" },
      { value: "en", name: "english" },
      { value: "ja", name: "japanese" },
      { value: "ko", name: "korean" },
      { value: "zh", name: "chinese" },
      { value: "pt", name: "portuguese" },
      { value: "fr", name: "french" },
      { value: "de", name: "german" },
      { value: "it", name: "italian" },
      { value: "ru", name: "russian" },
      { value: "ca", name: "catalan" },
      { value: "eu", name: "basque" },
      { value: "gl", name: "galician" },
      { value: "sv", name: "swedish" },
      { value: "no", name: "norwegian" },
      { value: "da", name: "danish" },
      { value: "fi", name: "finish" },
      { value: "pl", name: "polish" },
      { value: "ro", name: "romanian" },
      { value: "ar", name: "arabic" },
    ];
  }

  async block(user: User) {
    const alert = await this.alertCtrl.create({
      header: this.i18n.translate("do-you-want-to-block") + user.username + "?",
      message: this.i18n.translate("block-description"),
      inputs: [
        {
          name: "note",
          type: "text",
          placeholder: this.i18n.translate("block-reason"),
        },
      ],
      buttons: [
        {
          text: this.i18n.translate("cancel"),
          role: "cancel",
          cssClass: "secondary",
        },
        {
          text: this.i18n.translate("block-user"),
          role: "block",
          handler: async (data) => {
            try {
              await this.rest.put("block", { user: user.id, note: data.note });
              (
                await this.toast.create({
                  message: this.i18n.translate("user-blocked-successfully"),
                  duration: 2000,
                  position: "bottom",
                })
              ).present();
            } catch (e) {
              (
                await this.toast.create({
                  message: this.i18n.translate("error-blocking-user") + e,
                  duration: 2000,
                  position: "bottom",
                })
              ).present();
              await alert.present();
            }
          },
        },
      ],
      cssClass: "round-alert",
    });

    await alert.present();
    await alert.onDidDismiss();
  }
}
