import {
  ChangeDetectorRef,
  Component,
  Input,
  OnInit,
  ViewChild,
} from "@angular/core";
import { Router } from "@angular/router";
import { Keyboard } from "@capacitor/keyboard";
import {
  AlertController,
  IonTextarea,
  ModalController,
  ToastController,
  isPlatform,
} from "@ionic/angular";
import SwiperCore, {
  Keyboard as SwiperKeyboard,
  SwiperOptions,
  Pagination,
  Autoplay,
  Mousewheel,
} from "swiper";

import { Story } from "src/app/models/story";
import { User } from "src/app/models/user";
import { AuthService } from "src/app/services/auth.service";
import { UrlService } from "src/app/services/url.service";
import { UserService } from "src/app/services/user.service";
import { StoryService } from "../../services/story.service";
import { CommentLikesModal } from "../comment-likes/comment-likes.modal";
import { StoryModal } from "../story-modal/story.modal";
import { Haptics, NotificationType } from "@capacitor/haptics";
import { transition, trigger, useAnimation } from "@angular/animations";
import { pulse } from "ng-animate";
import { UtilsService } from "src/app/services/utils.service";
import { I18nService } from "src/app/services/i18n.service";

SwiperCore.use([SwiperKeyboard, Pagination, Autoplay, Mousewheel]);

@Component({
  selector: "view-stories-modal",
  templateUrl: "./view-stories.modal.html",
  styleUrls: ["./view-stories.modal.scss"],
  animations: [trigger("pulse", [transition("* => *", useAnimation(pulse))])],
})
export class ViewStoriesModal implements OnInit {
  @Input() stories: Story[];
  @ViewChild("textarea", { static: false })
  textarea: IonTextarea;
  public slides: SwiperCore;
  public story: Story;
  public showComments = false;
  public showCommentOptions = false;
  public selectedComment: Story["comments"][0];
  public showViews = false;
  public pulse: any;

  private delay = 5000;
  private inputAt = false;
  private mention: string;
  public userMentions: User["username"][] = [];
  public usernames: User[];
  private writing = false;

  public slideOpts: SwiperOptions = {
    speed: 500,
    autoplay: {
      delay: this.delay,
    },
    grabCursor: true,
    lazy: true,
    keyboard: true,
    pagination: {
      type: "progressbar",
    },
    mousewheel: true,
  };

  constructor(
    public thisModal: ModalController,
    private modalCreate: ModalController,
    private likeModal: ModalController,
    private storySvc: StoryService,
    public userSvc: UserService,
    private toast: ToastController,
    private router: Router,
    public auth: AuthService,
    private urlSvc: UrlService,
    private cd: ChangeDetectorRef,
    private alertCtrl: AlertController,
    private utils: UtilsService,
    private i18n: I18nService
  ) {}

  async ngOnInit() {
    this.viewStory(this.stories[0]);
    this.story = this.stories[0];
    // await this.utils.toggleTransparent();
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.slides.autoplay.start();
    }, 100);
  }

  setSwiperInstance(swiper: any) {
    this.slides = swiper;
    this.slides.update();
    this.slides.on("slideChange", async (event) => {
      this.slide(event.activeIndex);
      await Haptics.vibrate({ duration: 10 });
    });
  }

  async showProfile(id: User["id"]) {
    if (id !== 1) {
      this.close();
      this.router.navigate(["/profile", id]);
    }
  }

  slide(index: number) {
    this.story = this.stories[index];
    this.cd.detectChanges();
    this.viewStory(this.stories[index]);
  }

  touchStart() {
    this.slides.autoplay.stop();
  }

  touchEnd() {
    this.slides.autoplay.start();
  }

  async tap(event: any) {
    if (event instanceof PointerEvent) {
      const modal = await this.thisModal.getTop();
      const centerStart = modal.offsetWidth / 2; // 50px para el centro
      const centerEnd = modal.offsetWidth / 2; // 50px para el centro

      if (event.clientX > centerEnd) {
        this.slides.slideNext();
      } else if (event.clientX < centerStart) {
        this.slides.slidePrev();
      }
      // Si el evento está en el centro, no hagas nada
    }
    this.slides.autoplay.start();
  }

  closeCommentsSheet() {
    this.showComments = false;
  }

  showCommentOptionsSheet(comment: Story["comments"][0]) {
    this.selectedComment = comment;
    this.showCommentOptions = true;
  }

  closeCommentOptionsSheet() {
    this.showCommentOptions = false;
  }

  commentFocus(event?: CustomEvent) {
    event?.stopPropagation();
    if (event) {
      const textarea = event.target as unknown as IonTextarea;
      textarea.getInputElement().then((a) => a.blur());
    }
    this.showComments = true;
    setTimeout(() => {
      this.textarea.setFocus();
      if (isPlatform("capacitor")) {
        Keyboard.show();
      }
    }, 500);
  }

  showViewsSheet(event: Event) {
    event.stopPropagation();
    this.showViews = true;
  }

  closeViewsSheet() {
    this.showViews = false;
  }

  showCommentsSheet(event: Event) {
    event.stopPropagation();
    this.showComments = true;
  }

  closeCommentSheet() {
    this.showComments = false;
  }

  async switchLikeStory(story: Story, event: Event) {
    event.stopPropagation();
    this.slides.autoplay.stop();
    try {
      // Envía la solicitud al servidor en segundo plano
      if (story.like) {
        story = await this.storySvc.unlike(story.id);
        Haptics.notification({ type: NotificationType.Error });
      } else {
        story = await this.storySvc.like(story.id);
        Haptics.notification({ type: NotificationType.Success });
      }

      // actualizamos los stories
      this.stories = this.stories.map((s) => (s.id === story.id ? story : s));
    } catch (error) {
      console.error(`Error al cambiar el estado del "like": ${error}`);
    }

    this.slides.autoplay.start();
  }

  async switchLikeComment(comment: Story["comments"][0]) {
    this.slides.autoplay.stop();

    const liked = comment.likes.some(
      (l) => l.id === this.auth.currentUserValue.id
    );

    // Cambia el estado del "like" inmediatamente
    this.story.comments = this.story.comments.map((c) => {
      if (c.id === comment.id) {
        c.like = !liked;
      }
      return c;
    });

    try {
      // Envía la solicitud al servidor en segundo plano
      if (liked) {
        this.story = await this.storySvc.unlikeComment(comment.id);
        this.story.comments = this.story.comments.map((c) => {
          if (c.id === comment.id) {
            c.like = false;
          }
          return c;
        });
      } else {
        this.story = await this.storySvc.likeComment(comment.id);
        this.story.like = true;
        this.story.comments = this.story.comments.map((c) => {
          if (c.id === comment.id) {
            c.like = true;
          }
          return c;
        });
      }
    } catch (error) {
      // Si la solicitud falla, revierte el cambio y muestra un mensaje de error
      this.story.comments = this.story.comments.map((c) => {
        if (c.id === comment.id) {
          c.like = liked;
        }
        return c;
      });
      console.error(
        `Error al cambiar el estado del "like" del comentario: ${error}`
      );
    }
  }

  async viewStory(story: Story) {
    if (
      this.auth.currentUserValue.id !== story.user.id &&
      !story.viewStories.some(
        (v) => v.user.id === this.auth.currentUserValue.id
      )
    ) {
      this.storySvc.viewStory(story.id);
      story.viewStories = [
        ...story.viewStories,
        {
          date: new Date().toISOString(),
          user: this.auth.currentUserValue,
        },
      ];
      this.stories = this.stories.map((s) => (s.id === story.id ? story : s));
    }
  }

  async sendComment(event?: Event) {
    if (event) {
      event.preventDefault();
    }
    this.slides.autoplay.stop();
    const text = this.textarea.value.trim();
    this.textarea.value = "";
    this.story = await this.storySvc.commentStory(
      this.story.id,
      text,
      this.userMentions
    );
    this.stories = this.stories.map((story) =>
      story.id === this.story.id ? this.story : story
    );
  }

  async addStory() {
    const modal = await this.modalCreate.create({
      component: StoryModal,
      keyboardClose: true,
      showBackdrop: true,
      cssClass: "vertical-modal",
    });

    await modal.present();
    await modal.onDidDismiss();
  }

  async removeStory(story: Story) {
    await this.utils.delay(500);
    this.slides.autoplay.stop();
    try {
      await this.storySvc.deleteStory(story.id);
      (
        await this.toast.create({
          message: this.i18n.translate("story-deleted-successfully"),
          position: "middle",
          duration: 2000,
        })
      ).present();
      this.close();
    } catch (e) {
      (
        await this.toast.create({
          message: this.i18n.translate("error-deleting-story"),
          duration: 2000,
          position: "middle",
          color: "danger",
        })
      ).present();
    }
    this.slides.autoplay.start();
    this.modalCreate.dismiss();
  }

  async reportStory(story: Story) {
    await this.modalCreate.dismiss();
    const alert = await this.alertCtrl.create({
      header: this.i18n.translate("do-you-want-to-report-story", {
        username: story.user.username,
      }),
      message: this.i18n.translate("we-will-review-the-case"),
      inputs: [
        {
          name: "note",
          type: "text",
          placeholder: this.i18n.translate("report-reason"),
        },
      ],
      buttons: [
        {
          text: this.i18n.translate("cancel"),
          role: "cancel",
          cssClass: "secondary",
        },
        {
          text: this.i18n.translate("report"),
          role: "block",
          handler: async (data: any) => {
            if (data.note.trim().length) {
              try {
                await this.storySvc.report(story, data.note);
                (
                  await this.toast.create({
                    message: this.i18n.translate("story-reported-successfully"),
                    duration: 2000,
                    position: "bottom",
                  })
                ).present();
              } catch (e) {
                (
                  await this.toast.create({
                    message: this.i18n.translate("error-reporting-story", {
                      error: e,
                    }),
                    duration: 2000,
                    position: "bottom",
                    color: "danger",
                  })
                ).present();
                alert.present();
              }
            } else {
              (
                await this.toast.create({
                  message: this.i18n.translate(
                    "report-message-cannot-be-blank"
                  ),
                  duration: 2000,
                  position: "middle",
                  color: "danger",
                })
              ).present();

              this.reportStory(story);
            }
          },
        },
      ],
      cssClass: "round-alert",
    });

    await alert.present();
    this.slides.autoplay.stop();

    alert.onDidDismiss().then(() => {
      this.slides.autoplay.start();
    });
  }

  async reportComment(comment: Story["comments"][0]) {
    await this.modalCreate.dismiss();
    const alert = await this.alertCtrl.create({
      header: this.i18n.translate("do-you-want-to-report-comment", {
        username: comment.user.username,
      }),
      message: this.i18n.translate("we-will-review-the-case"),
      inputs: [
        {
          name: "note",
          type: "text",
          placeholder: this.i18n.translate("report-reason"),
        },
      ],
      buttons: [
        {
          text: this.i18n.translate("cancel"),
          role: "cancel",
          cssClass: "secondary",
        },
        {
          text: this.i18n.translate("report"),
          role: "block",
          handler: async (data: any) => {
            if (data.note.trim().length) {
              try {
                await this.storySvc.reportComment(comment.id, data.note);
                (
                  await this.toast.create({
                    message: this.i18n.translate(
                      "comment-reported-successfully"
                    ),
                    duration: 2000,
                    position: "bottom",
                  })
                ).present();
              } catch (e) {
                (
                  await this.toast.create({
                    message: this.i18n.translate("error-reporting-comment", {
                      error: e,
                    }),
                    duration: 2000,
                    position: "bottom",
                    color: "danger",
                  })
                ).present();
                alert.present();
              }
            } else {
              (
                await this.toast.create({
                  message: this.i18n.translate(
                    "report-message-cannot-be-blank"
                  ),
                  duration: 2000,
                  position: "middle",
                  color: "danger",
                })
              ).present();

              this.reportComment(comment);
            }
          },
        },
      ],
      cssClass: "round-alert",
    });

    await alert.present();
    await alert.onDidDismiss();
  }

  async blockUser(user: User) {
    try {
      await this.userSvc.block(user);
      this.router.navigate(["/"]);
    } catch (e) {
      // No hacer nada
    }
    this.close();
  }

  async viewCommentLikes(likes: Story["comments"][0]["likes"]) {
    const modal = await this.likeModal.create({
      component: CommentLikesModal,
      keyboardClose: true,
      showBackdrop: true,
      componentProps: { likes },
      cssClass: "medium-modal",
    });

    await modal.present();
    const data = await modal.onDidDismiss();
    if (data?.data?.showProfile) {
      this.close();
    }
  }

  async reply(comment: Story["comments"][0]) {
    if (comment.user.id !== this.auth.currentUserValue.id) {
      this.textarea.value = `@${comment.user.username} `;
      this.setMention(comment.user.username);
    }
    this.commentFocus();
    if (isPlatform("capacitor")) {
      Keyboard.show();
    }
  }

  setMention(username: string) {
    this.usernames = [];
    this.inputAt = false;
    this.textarea.value = this.textarea.value.replace(
      this.mention,
      `@${username} `
    );
    this.userMentions = [...this.userMentions, username];
    this.commentFocus();
    if (isPlatform("capacitor")) {
      Keyboard.show();
    }
  }

  async setWriting(text: string) {
    if (this.textarea.value) {
      if (text.charAt(text.length - 1) == "@") {
        this.inputAt = true;
      }

      if (this.inputAt) {
        const pattern = /\B@[a-zA-Z0-9-_.À-ÿ\u00f1\u00d1 ]+/gi;
        const matches = text.match(pattern);
        if (matches) {
          this.mention = matches[matches.length - 1];
        }

        if (this.writing) {
          return;
        }
        this.writing = true;

        if (this.mention?.length > 3 && this.textarea.value.length > 3) {
          this.usernames = await this.userSvc.searchUsernames(
            this.mention.replace("@", "")
          );
        } else {
          this.usernames = [];
        }

        setTimeout(async () => {
          this.writing = false;
        }, 500);
      }
    } else {
      this.usernames = [];
    }
  }

  async deleteComment(comment: Story["comments"][0]) {
    (
      await this.toast.create({
        message: this.i18n.translate("deleting-comment"),
        position: "middle",
        duration: 2000,
      })
    ).present();
    try {
      await this.storySvc.deleteComment(comment.id);
      this.story.comments = this.story.comments.filter(
        (c) => c.id !== comment.id
      );

      (
        await this.toast.create({
          message: this.i18n.translate("comment-deleted-successfully"),
          position: "middle",
          duration: 2000,
        })
      ).present();
      this.showCommentOptions = false;
    } catch (e) {
      (
        await this.toast.create({
          message: this.i18n.translate("error-deleting-comment"),
          duration: 2000,
          position: "middle",
          color: "danger",
        })
      ).present();
    }
  }

  async openUrl(event: any) {
    await this.urlSvc.openUrl(event);

    if (
      event.srcElement.href &&
      (event.target.className.includes("mention") ||
        event.target.className.includes("hashtag") ||
        event.target.className.includes("linkified"))
    ) {
      this.close();
    }

    return false;
  }

  showPage(slug: string) {
    this.close();
    this.router.navigate(["/page", slug]);
  }

  async close() {
    if (await this.modalCreate.getTop()) {
      await this.modalCreate.dismiss();
    }
    if (await this.thisModal.getTop()) {
      this.thisModal.dismiss();
    }
  }
}
